import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';

export class Rank {
  id: string;
  name: string;
  level: string;
}

@Injectable({
  providedIn: 'root'
})
export class RankingServiceService {

  // Base url
  baseurl = 'https://calculationgame-eb2c.restdb.io/rest/ranking';

  // Http Headers
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'x-apikey': '333973587c7101ea9b778c80e2c03a48d82f1',
      'cache-control': 'no-cache',
      'Access-Control-Allow-Origin': '*'
    })
  };

  constructor(private http: HttpClient) { }

  // POST
  CreateRank(data: any): Observable<Rank> {
    return this.http.post<Rank>(this.baseurl, JSON.stringify(data), this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.errorHandl)
      );
  }

  // GET
  GetRank(id: any): Observable<Rank> {
    return this.http.get<Rank>(this.baseurl + '/' + id, this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.errorHandl)
      );
  }

  // GET ALL
  GetRanks(): Observable<Rank> {
    return this.http.get<Rank>(this.baseurl, this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.errorHandl)
      );
  }

  // PUT
  UpdateRank(id: any, data: any): Observable<Rank> {
    return this.http.put<Rank>(this.baseurl + '/' + id, JSON.stringify(data), this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.errorHandl)
      );
  }

  // DELETE
  DeleteRank(id: any) {
    return this.http.delete<Rank>(this.baseurl + '/' + id, this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.errorHandl)
      );
  }

  // Error handling
  errorHandl(error: { error: { message: any; }; status: any; message: any; }) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);
    return throwError(errorMessage);
  }

}
